body {
  padding: 0;
  margin: 0;
  font-family: "Spectral", "Times New Roman", serif;
  font-size: 16px;
  line-height: 22px;
  color: #333333;
  background-color: #ffffff; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-touch-callout: none;
  -khtml-user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent; }

.slick-list {
  position: relative;
  display: block;
  overflow: hidden;
  margin: 0;
  padding: 0; }

.slick-list:focus {
  outline: none; }

.slick-list.dragging {
  cursor: pointer;
  cursor: hand; }

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0); }

.slick-track {
  position: relative;
  top: 0;
  left: 0;
  display: block;
  margin-left: auto;
  margin-right: auto; }

.slick-track:before,
.slick-track:after {
  display: table;
  content: ''; }

.slick-track:after {
  clear: both; }

.slick-loading .slick-track {
  visibility: hidden; }

.slick-slide {
  display: none;
  float: left;
  height: 100%;
  min-height: 1px; }

[dir='rtl'] .slick-slide {
  float: right; }

.slick-slide img {
  display: block; }

.slick-slide.slick-loading img {
  display: none; }

.slick-slide.dragging img {
  pointer-events: none; }

.slick-initialized .slick-slide {
  display: block; }

.slick-loading .slick-slide {
  visibility: hidden; }

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent; }

.slick-arrow.slick-hidden {
  display: none; }

.wrapper {
  max-width: 1240px;
  padding: 0 20px;
  margin: 0 auto;
  box-sizing: border-box; }

.title {
  font-size: 34px;
  line-height: 44px;
  font-weight: bold;
  color: #333333; }

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 214px;
  height: 60px;
  font-size: 24px;
  line-height: 28px;
  color: #ffffff;
  text-decoration: none;
  background-color: #631421;
  border: 2px solid #631421;
  border-radius: 5px;
  box-sizing: border-box;
  cursor: pointer;
  outline: none;
  transition: 0.7s; }
  .button:hover {
    color: #631421;
    background-color: #ffffff; }
  .button:active {
    opacity: 0.5;
    transition: 0.1s; }

.button--modal-open {
  margin-bottom: 30px; }

.menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 120px; }
  @media screen and (max-width: 1023px) {
    .menu {
      height: auto;
      min-height: 80px;
      padding: 20px 0; } }

.menu__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-grow: 1;
  height: 100%; }
  @media screen and (max-width: 1023px) {
    .menu__list {
      display: none;
      flex-direction: column; } }
  @media screen and (max-width: 767px) {
    .menu__list {
      position: absolute;
      top: 91px;
      left: 0;
      background: #ffffff;
      width: 100%;
      height: auto; } }

.menu__user {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-left: 100px; }
  @media screen and (max-width: 1023px) {
    .menu__user {
      margin-left: 10px; } }

.menu__item {
  position: relative;
  display: flex;
  align-items: center;
  height: 100%;
  padding: 0 5px;
  transition: 0.5s; }
  .menu__item.active .menu__arrow {
    transform: rotate(225deg); }
  @media screen and (max-width: 1023px) {
    .menu__item {
      display: block;
      height: auto; } }
  @media screen and (max-width: 767px) {
    .menu__item:first-child {
      padding-top: 10px; } }
  @media screen and (max-width: 767px) {
    .menu__item:last-child {
      padding-bottom: 10px; } }

@media screen and (max-width: 1023px) {
  .menu__item:not(:last-child) {
    margin-bottom: 20px; } }

.menu__item ul {
  display: none;
  position: absolute;
  top: 120px;
  left: 0;
  min-width: 292px;
  padding: 12px;
  border: 1px solid #CFAF7D;
  background-color: #ffffff;
  z-index: 10; }
  @media screen and (max-width: 1023px) {
    .menu__item ul {
      position: relative;
      top: auto;
      left: auto;
      border: none; } }

.menu__subitem {
  padding-bottom: 8px; }
  .menu__subitem a {
    font-size: 16px;
    line-height: 24px; }

.menu__subitem:not(:last-child) {
  margin-bottom: 8px;
  border-bottom: 1px solid #F7F4ED; }
  @media screen and (max-width: 1023px) {
    .menu__subitem:not(:last-child) {
      border: none; } }

.menu__link {
  display: flex;
  align-items: center;
  font-family: "Spectral", "Times New Roman", serif;
  font-size: 18px;
  line-height: 27px;
  color: #333333;
  text-decoration: none;
  transition: 0.5s; }
  .menu__link:hover {
    color: #b8253d; }
    @media screen and (max-width: 1023px) {
      .menu__link:hover {
        color: #333333; } }
  @media screen and (max-width: 1023px) {
    .menu__link {
      justify-content: center; } }
  .menu__link.active {
    color: #631421; }

.menu__articles {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 270px;
  height: 60px;
  font-family: "Spectral", "Times New Roman", serif;
  margin-right: 60px;
  font-weight: 500;
  font-size: 18px;
  line-height: 23px;
  color: #631421;
  text-decoration: none;
  border: 1px solid #631421;
  border-radius: 4px;
  cursor: pointer;
  transition: 0.5s; }
  .menu__articles:hover {
