.menu__languages {
  display: flex;
  margin-left: 60px; }
  @media screen and (max-width: 1365px) {
    .menu__languages {
      margin-left: 30px; } }

.menu__language {
  padding: 5px;
  font-family: "PTMono", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: #333333;
  border-radius: 3px;
  text-decoration: none;
  transition: 0.5s; }
  .menu__language.active {
    color: #ffffff;
    background-color: #631421; }
  .menu__language:hover {
    color: #ffffff;
    background-color: #631421; }

.menu__arrow {
  display: block;
  width: 8px;
  height: 8px;
  margin-left: 8px;
  border-right: 1px solid #333333;
  border-bottom: 1px solid #333333;
  transform: rotate(45deg);
  transition: 0.5s; }

.menu__link-button {
  position: relative;
  transform: translate(-10px, 0); }
  .menu__link-button:after {
    content: "";
    display: block;
    position: absolute;
    width: 10px;
    height: 10px;
    top: 50%;
    right: 0;
    transform: translate(22px, -45%) rotate(-45deg);
    border-bottom: 1px solid #631421;
    border-right: 1px solid #631421; }

.icon-login {
  display: block;
  cursor: pointer; }
  .icon-login:hover {
    opacity: 0.5; }

.burger {
  display: none;
  position: relative;
  width: 50px;
  height: 50px;
  background-color: #ffffff;
  border: 1px solid #631421;
  border-radius: 50%;
  flex-shrink: 0; }
  @media screen and (max-width: 1023px) {
    .burger {
      display: block;
      margin-right: 10px; } }

.burger__strip,
.burger__strip:before,
.burger__strip:after {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -1px;
  margin-left: -10px;
  width: 20px;
  height: 2px;
  background-color: #631421; }

.burger__strip:before,
.burger__strip:after {
  content: "";
  transition: 0.3s; }

.burger__strip:before {
  transform: translateY(-5px); }

.burger__strip:after {
  transform: translateY(5px); }

.burger--active .burger__strip {
  height: 0; }

.burger--active .burger__strip:before {
  transform: rotate(45deg); }

.burger--active .burger__strip:after {
  transform: rotate(-45deg); }

